import type { IStringDictionary } from "../../../../../base/common/collections.js";
import { localize } from "../../../../../nls.js";
import type { IConfigurationPropertySchema } from "../../../../../platform/configuration/common/configurationRegistry.js";
import product from "../../../../../platform/product/common/product.js";

export enum TerminalStickyScrollSettingId {
	Enabled = "terminal.integrated.stickyScroll.enabled",
	MaxLineCount = "terminal.integrated.stickyScroll.maxLineCount",
}

export interface ITerminalStickyScrollConfiguration {
	enabled: boolean;
	maxLineCount: number;
}

export const terminalStickyScrollConfiguration: IStringDictionary<IConfigurationPropertySchema> =
	{
		[TerminalStickyScrollSettingId.Enabled]: {
			markdownDescription: localize(
				"stickyScroll.enabled",
				"Shows the current command at the top of the terminal.",
			),
			type: "boolean",
			default: product.quality !== "stable",
		},
		[TerminalStickyScrollSettingId.MaxLineCount]: {
			markdownDescription: localize(
				"stickyScroll.maxLineCount",
				"Defines the maximum number of sticky lines to show. Sticky scroll lines will never exceed 40% of the viewport regardless of this setting.",
			),
			type: "number",
			default: 5,
			minimum: 1,
			maximum: 10,
		},
	};
